export default { 
	data() {
		return {
			active: null,
			title: '',
			city: {
				from: '北京',
				to: '成都'
			},

			banchelist: [
			],
			// 高铁列表数组
			gaotieList:[
				
			],
			nowTime:'', // 选中日期时间
			ticdes:{
				FromStation:'北京',
				ToStation: '上海'
			},// 当前票务信息
		}
	},
	onUnload(){
		uni.hideLoading()
	},
	onLoad(opt) {
		// this.getGaoTieList(opt)
		console.log('opt', opt)
		this.ticdes = opt
		this.nowTime = opt.FromDate
		this.setTopTimes()
		this.choseOneTic(this.banchelist[0],0)
	},
	onUnload(){
		uni.hideLoading()
	},
	methods: {
		// 选择某一张高铁票 跳转高铁详情
		choseGtDet(item,idx){
			let itemP = this.gaotieList[idx]
			this.$store.commit('setGtYuTic',itemP)
			uni.navigateTo({
				url:'../gtorderconfirm/gtorderconfirm'
			})
		},
		
		
		
		// 设置周末数值展示
		setWeekStr(str){
			let week = ''
			switch (str){
				case 1:week = '周一';break;
				case 2:week = '周二';break;
				case 3:week = '周三';break;
				case 4:week = '周四';break;
				case 5:week = '周五';break;
				case 6:week = '周六';break;
				case 0:week = '周日';break;
			}
			return week
		},
		// 设置顶部日期滚动条选择不同时间
		setTopTimes(){
			// 设置跨月依然游刃有余 skr
			let time = this.nowTime
			// let time = '2020-3-30'
			let timeSeconds = new Date(time).getTime()
			let oneDate = {
			}
			let box = []
			// return
			// i 小于到数值就是展示多少条日期的数值
			for (let i = 0;i<4;i++) {
				let year = new Date(timeSeconds).getFullYear() // 获取到年
				let month = new Date(timeSeconds).getMonth() + 1 // 获取到月
				let day = new Date(timeSeconds).getDate() // 获取到日期
				let showDay = month + '/' + day
				let week = new Date(timeSeconds).getDay() // 获取到周末
				 oneDate = {
					day:showDay,
					week:week
				}
				box.push(oneDate)
				timeSeconds = timeSeconds + 86400000
			}
			console.log(box)
			this.banchelist = box
		},
		 
		// 选择某个日期的票
		choseOneTic(item, idx) {
			console.log('item',item)
			let itemO = this.banchelist[idx]
			this.active == idx ? this.active = null : this.active = idx
			let opt = this.ticdes
			let year = new Date(this.ticdes.FromDate).getFullYear()
			opt.FromDate = year+'/'+itemO.day
			console.log(opt.FromDate,'选中的日期')
			this.$store.commit('setGtTic', opt.FromDate)
			// return
			this.getGaoTieList(opt)
			
		},
		
		// 处理下动车类型展示
		setTrainType(type){
			let str = ''
			switch(type){
				case 0:str = '高铁';break;
				case 1:str = '动车';break;
				case 2:str = '直达';break;
				case 3:str = '特快';break;
				case 4:str = '快速';break;
				case 9:str = '其他';break;
			}
			return str
		},
		
		
		// 获取高铁列表
		getGaoTieList(opt){
			// return
			var _this = this
			this.gaotieList = []
			var Timestamp = new Date().toLocaleString()
			uni.showLoading({
				title:'加载中'
			})
			let url = 'http://apitest.99263.com'
			uni.request({
				url: this.$slurl + "/Train/Query",
				method:"POST",
				data:{
					  "ApiKey": this.$ApiKey,
					  "Sign": "",
					  "Timestamp": Timestamp,
					  "Data": {
						"FromStation": opt.FromStation,
						"ToStation": opt.ToStation,
						"FromDate": opt.FromDate
					  }
					},
				success:function(res){
					// console.log(res.data.Data,'高铁列表')
					uni.hideLoading()
					if (res.data.IsSuccess) {
					_this.gaotieList = res.data.Data
					} else {
						uni.showModal({
							showCancel:false,
							title:'提示',
							content:'无符合车次'
						})
					}
				}
				
			})
		},
		// 返回上级目录
		goBack(){
			uni.navigateBack({
				
			})
		}
	
	}
}
